@import 'categories/banners';
.categories-main{
  .brands__title{
    border: none;
    padding-bottom: 0;
  }

  @include adaptiveValue("margin-bottom", 90, 30);
}
.page {
  &__categories {
  }
}

.categories-page {
  &__container {
  }

  &__menu {
    margin-top: rem(20);
  }
  .popular-categories {
    &__content {
      border: none;
      padding: rem(70) 0;
      background: linear-gradient(92.44deg, #94A073 0%, #747D59 100%);
      position: relative;
      overflow: hidden;
      &:before{
        content: "";
        position: absolute;
        width: percent(519, 1860);
        height: 200%;
        @media(max-width:$pc){
          background-size: 200%;
        }
        left: percent(1555,1860);
        top: percent(-150, 430);
        z-index: 5;
        background: url("../../img/Categories/right-bg.png") center/cover no-repeat;
      }
      &:after{
        content: "";
        position: absolute;
        width: percent(519, 1860);
        height: 200%;
        @media(max-width:$pc){
          background-size: 200%;
        }
        right: percent(1505,1860);
        top: percent(-150, 430);
        z-index: 5;
        background: url("../../img/Categories/left-bg.png") center/cover no-repeat;
      }
      color: #FFFFFF;
    }

    &__menu {
    }

    &__list {
    }

    &__list-item {

    }

    &__list-link {
      @media(any-hover: hover){
        &:hover{
          color: #49542A;
        }
      }
    }
  }
}

